:host {
  display: block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: visible !important;
}

header {
  will-change: backdrop-filter;
  overflow: visible !important;
}
header::before {
  content: "";
  position: absolute;
  inset: 0;
  backdrop-filter: inherit;
  z-index: -1;
}

.logo-container img {
  will-change: transform, filter;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.logo-container img:hover {
  transform: scale(1.05) translateZ(0);
}

:global(.active-link) {
  position: relative;
  color: var(--sned-blue) !important;
}
:global(.active-link) > span {
  transform: scaleX(1) !important;
  background: linear-gradient(45deg, var(--sned-blue), var(--sned-orange)) !important;
}

:global(.active-mobile) {
  background: linear-gradient(to right, rgba(248, 152, 81, 0.1), rgba(19, 157, 204, 0.05)) !important;
  color: var(--sned-blue) !important;
  border-left: 4px solid var(--sned-orange) !important;
  padding-left: 12px !important;
}

:global(.active-mobile-sub) {
  background: linear-gradient(to right, rgba(248, 152, 81, 0.08), rgba(19, 157, 204, 0.04)) !important;
  color: var(--sned-blue) !important;
}

@media (max-width: 640px) {
  .hero-section {
    padding-top: theme("spacing.16");
  }
  .hero-section h1 {
    font-size: clamp(1.875rem, 8vw, 3rem);
    line-height: 1.1;
  }
  .hero-section p {
    font-size: clamp(1rem, 4vw, 1.25rem);
  }
}
@media (min-width: 641px) and (max-width: 1024px) {
  .hero-section {
    padding-top: theme("spacing.20");
  }
  .hero-section h1 {
    font-size: clamp(2.25rem, 6vw, 4rem);
  }
}
@media (min-width: 1025px) {
  .hero-section {
    padding-top: theme("spacing.24");
  }
  .hero-section h1 {
    font-size: clamp(3rem, 4vw, 5rem);
  }
}
.animate-element {
  will-change: transform, opacity;
  backface-visibility: hidden;
  perspective: 1000px;
}

.scrollable-area {
  scrollbar-width: thin;
  scrollbar-color: var(--sned-orange) transparent;
}
.scrollable-area::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
.scrollable-area::-webkit-scrollbar-track {
  background: transparent;
}
.scrollable-area::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, var(--sned-orange), var(--sned-blue));
  border-radius: 2px;
}
.scrollable-area::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(to bottom, var(--sned-orange-dark), var(--sned-blue-dark));
}

.focus-visible {
  outline: 2px solid var(--sned-orange);
  outline-offset: 2px;
  border-radius: 4px;
}

@media (prefers-reduced-motion: no-preference) {
  .smooth-transition {
    transition-duration: 0.3s;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
  .bounce-animation {
    animation: gentle-bounce 2s infinite;
  }
  @keyframes gentle-bounce {
    0%, 20%, 53%, 80%, 100% {
      transform: translate3d(0, 0, 0);
    }
    40%, 43% {
      transform: translate3d(0, -8px, 0);
    }
    70% {
      transform: translate3d(0, -4px, 0);
    }
    90% {
      transform: translate3d(0, -2px, 0);
    }
  }
  .pulse-animation {
    animation: soft-pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  }
  @keyframes soft-pulse {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0.7;
    }
  }
}
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
  .bounce-animation,
  .pulse-animation {
    animation: none !important;
  }
}
@media (prefers-contrast: high) {
  :global(.active-link) {
    background-color: var(--sned-orange) !important;
    color: white !important;
    border-radius: 4px;
    padding: 4px 8px;
  }
  :global(.active-link) > span {
    background: white !important;
  }
  .hero-section {
    background: black !important;
  }
  .hero-section h1, .hero-section p {
    color: white !important;
  }
  .hero-section .gradient-text {
    background: white !important;
    color: white !important;
  }
}
@media print {
  header {
    position: static !important;
    background: white !important;
    color: black !important;
    box-shadow: none !important;
  }
  .hero-section {
    display: none;
  }
}
[dir=rtl] .navigation-menu .dropdown-menu {
  left: auto;
  right: 0;
}
[dir=rtl] .navigation-menu .dropdown-menu::before {
  left: auto;
  right: 24px;
}
[dir=rtl] .language-selector .lang-dropdown {
  left: 0;
  right: auto;
}
[dir=rtl] .language-selector .lang-dropdown::before {
  left: 16px;
  right: auto;
}

.loading-shimmer {
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}
app-language-selector,
app-navigation-menu,
app-mobile-menu {
  contain: layout style;
}

h1, h2, h3, h4, h5, h6 {
  text-rendering: optimizeLegibility;
  font-feature-settings: "liga" 1, "kern" 1;
}

.interactive-element {
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}
.interactive-element:hover {
  transform: translateY(-1px);
}
.interactive-element:active {
  transform: translateY(0);
  transition-duration: 0.1s;
}
